import { Skeleton, Spin } from 'ant-design-vue';
import Vue from 'vue';
var selectorPrefix = 'adherev-ui-suspense';
var Suspense = Vue.extend({
  props: {
    reset: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      // 第一次
      isFirst: true,
      // 第一次加载
      isFirstLoading: false
    };
  },
  watch: {
    reset: function reset(newVal) {
      if (newVal) {
        // 第一次
        this.isFirst = true; // 第一次加载

        this.isFirstLoading = false;
        this.$forceUpdate();
      }
    }
  },
  mounted: function mounted() {
    this.fetchData();
  },
  methods: {
    /**
     * renderNormalFirstLoading
     */
    renderNormalFirstLoading: function renderNormalFirstLoading(h) {
      var result = [];

      for (var i = 0; i < 7; i++) {
        // @ts-ignore
        result.push(h(Skeleton, {
          "key": i + 1,
          "attrs": {
            "loading": true,
            "active": true,
            "avatar": true
          }
        }));
      }

      return h("div", {
        "class": selectorPrefix + "-loading"
      }, [result]);
    },

    /**
     * renderFirstLoading
     * @param h
     */
    renderFirstLoading: function renderFirstLoading(h) {
      var $slots = this.$slots;

      if ($slots.firstLoading) {
        return $slots.firstLoading;
      }

      return this.renderNormalFirstLoading(h);
    },

    /**
     * renderNormal
     * @param h
     */
    renderNormal: function renderNormal(h) {
      return (// @ts-ignore
        h(Spin, {
          "attrs": {
            "size": "large",
            "spinning": this.showLoading()
          }
        }, [this.renderInner(h)])
      );
    },

    /**
     * renderDispatch
     * @param h
     */
    renderDispatch: function renderDispatch(h) {
      var loading = this.showLoading();

      if (this.isFirst && !this.isFirstLoading && loading) {
        this.isFirstLoading = true;
      }

      if (this.isFirst && this.isFirstLoading && !loading) {
        this.isFirst = false;
        this.isFirstLoading = false;
      }

      if (this.isFirst) {
        return this.renderFirstLoading(h);
      }

      return this.renderNormal(h);
    },

    /**
     * renderSuspense
     * @description - renderSuspense
     * @param h
     */
    renderSuspense: function renderSuspense(h) {
      return h("div", {
        "class": selectorPrefix
      }, [this.renderDispatch(h)]);
    }
  },

  /**
   * render
   * @param h
   */
  render: function render(h) {
    return this.renderSuspense(h);
  }
});
/**
 * Suspense
 * @overview showLoading: boolean
 * @overview renderInner: VNode | null
 * @overview fetchData(): void
 */

export default Suspense;